import React from 'react';
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';
import RemoveIcon from '@material-ui/icons/Remove';
import { TaskCheckbox } from './TaskCheckbox';
import { EditTaskEntity } from './EditTaskEntity';
import { DeleteTaskEntity } from './DeleteTaskEntity';
import { TaskPriorityControls } from './TaskPriorityControls';

export enum TaskStatus {
  IN_PROGRESS = 'inProgress',
  DONE = 'done',
}

export interface TaskI {
  id: number;
  name: string;
  deadline: string | null;
  order: number;
  status: TaskStatus;
}

interface OwnProps {
  task: TaskI;
  projectId: number;
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    container: {
      position: 'relative',
      display: 'flex',
      marginTop: theme.spacing(1),
      marginBottom: theme.spacing(1),
      alignItems: 'center',
      background: '#ffffff',
      '&:hover': {
        background: '#fcfed5',
        '& div:last-child': {
          display: 'flex',
        },
      },
    },
    iconContainer: {
      display: 'flex',
      flexDirection: 'row',
      position: 'absolute',
      alignItems: 'center',
      right: 10,
      '&:last-child': {
        // display: 'none',
      },
    },
    editIcon: {
      color: '#98b0d2',
      width: 24,
      height: 24,
    },
    deleteIcon: {
      color: '#98b0d2',
      width: 24,
      height: 24,
    },

    removeIcon: {
      color: '#98b0d2',
      transform: `rotate(90deg)`,
      '&:active': {
        transform: `translate(0px,0px), rotate(45deg)`,
      },
    },
    taskNameContainer: {
      color: '#a2a2a2',
      fontSize: 18,
      fontWeight: 600,
      marginLeft: theme.spacing(2),
    },
  }),
);

export const Task: React.FC<OwnProps> = ({ task, projectId }) => {
  const { container, removeIcon, taskNameContainer, iconContainer } = useStyles();

  return (
    <div className={container}>
      <TaskCheckbox task={task} projectId={projectId} />
      <div className={taskNameContainer}>{task.name}</div>
      <div className={iconContainer}>
        <TaskPriorityControls task={task} projectId={projectId} />
        <EditTaskEntity task={task} projectId={projectId} />
        <RemoveIcon className={removeIcon} />
        <DeleteTaskEntity task={task} projectId={projectId} />
      </div>
    </div>
  );
};
